.bingo-toggle-switch {
  display: inline-block;
  margin: 8px;
  width: 180px;
  height: 46px;
}

label {
  -webkit-perspective: 1000px;
  perspective: 1000px;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  width: 180px;
  height: 46px;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.mobile label {
    height: 46px;
    width: 149px;
}

.bingo-toggle {
  display: inline-block;
  position: relative;
  height: 100%;
  width: 100%;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  transition: all 600ms;
  z-index: 1000000000;
}

.mobile .bingo-toggle-switch {
  height: 46px;
  width: 149px;
}

.bingo-toggle div {
  margin-top: 50px;
  font-family: Verdana,Geneva,sans-serif;
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: white;
  line-height: 41px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 2px;
}

.bingo-toggle .back {
  background: #A9A9A9;
  color: black;
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
  border: solid;
  border-color: grey;
}
.bingo-toggle .front {
  border: solid;
  border-color: #C0C0C0;
}

.bingo-toggle-switch input {
  display: none;
}

:checked + .bingo-toggle {
  transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
}

 
